const formatOutput = require('@polkadot/api-format/output');

const assert = require('@polkadot/util/assert');

const ExtError = require('@polkadot/util/ext/error');

const isFunction = require('@polkadot/util/is/function');

const jsonrpcSignature = require('@polkadot/util/jsonrpc/signature');

const createParams = require('./params');

module.exports = function methodSubscribe(provider, rpcName, name, {
  inputs,
  output
}) {
  const unsubscribe = subscriptionId => provider.send(`unsubscribe_${name}`, [subscriptionId]);

  const call = async (..._params) => {
    try {
      // flowlint-next-line unclear-type:off
      const cb = _params.pop();

      assert(isFunction(cb), `Expected callback in last position of params`);
      const params = createParams(_params, inputs);

      const update = (error, result) => {
        cb(error, formatOutput(output, result));
      };

      return provider.subscribe(`subscribe_${name}`, params, update);
    } catch (error) {
      throw new ExtError(`${jsonrpcSignature(rpcName, inputs, output)}:: ${error.message}`, error.code);
    }
  };

  call.unsubscribe = unsubscribe; // flowlint-next-line unclear-type:off

  return call;
};